    border-top-right-radius: 9px;
    border-bottom-right-radius: 9px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    border: 1px solid #ff4655;
    cursor: pointer;
    -webkit-appearance: none;
  }
