.ControlTableContainer label {
  position: absolute;
  display: inline-block;
  width: 40px;
  height: 20px;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto 10px auto 0;
}

.ControlTableContainer label input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.widgetSlider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #162a44;
  -webkit-transition: .4s;
  transition: .4s;
}

.widgetSlider:before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 4px;
  bottom: 3px;
  background-color: #eeeeee;
  -webkit-transition: .4s;
  transition: .4s;
}

.ControlTableContainer label input:checked + .widgetSlider {
  background-color: #3bff6f;
}
.ControlTableContainer label input:checked + .widgetSlider:before {
  background-color: #001326;
}

.ControlTableContainer label input:focus + .widgetSlider {
  box-shadow: 0 0 1px #162a44;
}

.ControlTableContainer label input:checked + .widgetSlider:before {
  -webkit-transform: translateX(19px);
  -ms-transform: translateX(19px);
  transform: translateX(19px);
}

/* Rounded sliders */
.widgetSlider.round {
  border-radius: 34px;
}

.widgetSlider.round:before {
  border-radius: 50%;
}

