  box-sizing: border-box;
  border-left: 1px solid #162a44;
}
.FooterLinks ul {
  color: #a7acb0;
  list-style: none;
  padding-left: 30px;
}
.FooterLinks a {
  color: #a7acb0;
  text-decoration: none;
}
.FooterLinks li:nth-child(1) {
  font-size: 18px;
  color: #eee;
}
.RiotDisclaimer {
    color: #a7acb0;
    text-align: center;
    width: 90%;
    max-width: 1200px;
    position: relative;
    left: 0;
    right: 0;
    margin: 20px auto;
    font-size: 14px;
}
.Footer img {
    height: 40px;
