/* Login/Register/Logout Pages */

.LoginPageContainer {
  height: 100%;
}
.LoginPage {
  height: 100%;
}
.LoginBody {
  width: 100%;
	height: 100%;
	margin-right: auto;
	margin-left: auto;
	display:block;
	position: relative;
}
.LoginForm {
  width: 320px;
  height: 340px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
.RegisterForm {
  height: 430px !important;
}
.LoginForm h1 {
  text-align: center;
  font-size: 30px;
  line-height: 40px;
  margin: 15px 0;
}
.LoginForm label {
  width: 100%;
  display: block;
  text-align: center;
  line-height: 18px;
  font-size: 14px;
  color: #ff4655;
  padding: 15px 0 10px 0;
}
.LoginForm input {
  width: 80%;
  height: 40px;
  position: relative;
  display: block;
  left: 0;
  right: 0;
  margin: 0 auto;
  margin-top: 35px;
  background-color: #001326;
  color: #eee;
  border: none;
  border-bottom: 2px solid #162a44;
  text-align: center;
  font-size: 16px;
}
.LoginForm input:hover {
  border-bottom: 2px solid #ff4655;
}
.LoginForm input:focus {
  border-bottom: 2px solid #ff4655;
}
.LoginForm input[type=submit] {
  width: 80%;
  height: 50px;
  position: absolute;
  display: block;
  left: 0;
  right: 0;
  bottom: 30px;
  margin: 0 auto;
  background-color: #162a44;
  color: #001326;
  border: 2px solid #162a44;
  text-align: center;
  font-size: 18px;
  -webkit-appearance: none;
  border-radius: 0;
}
.LogOutBody {
  width: 100%;
	height: 100%;
	margin-right: auto;
	margin-left: auto;
	display:block;
	position: relative;
}
.LogOutForm {
  width: 200px;
  height: 60px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
.LogOutForm h1 {
  text-align: center;
  font-size: 26px;
  line-height: 60px;
  margin: 0;
}